      </v-btn>
      <span class="text_middle mt-3">火锅底料社区公约</span>
    </v-app-bar>
    <div >
      <iframe class="main_container_topPost" frameborder="x" src="http://localhost:8081/#/topPost"></iframe>
    </div>
  </v-container>
</template>

<script>
export default {
  name:'topPost'
}
</script>

<style>
.main_container_topPost{
  margin-top: 60px;
  width: 100%;
  height: 100vh;
}
</style>



